@import "../../assets/_design-tokens";

.navbar {
  display: flex;
  align-items: center;
  background-color: $token-dtet-next-primary-primary-darker;
  color: $token-dtet-next-base-base-white;
  height: 40px;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: rgba(255, 255, 255, 0.2);
  padding: 0px 8px 0px 8px;
  justify-content: space-between;
  background: $token-dtet-next-accent-cool-accent-cool-darker;
}

.left-header-group {
  display: flex;
  align-items: center;
  flex: 1 0 0;
}

.header-logo {
  font-weight: bold;
  height: 100%;
  display: flex;
  align-items: center;
  margin-right: 32px;
}

.community-text {
  color: $token-dtet-next-secondary-secondary-light;
}

.header-title {
  font-weight: bold;
  height: 100%;
  display: flex;
  align-items: center;
}

.patient-search-link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-bottom: 3px solid #c7a97b;
  padding: 0 2px;
  height: 100%;
}

.patient-search-link-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 0 0;
  height: 100%;
  gap: 32px;
}

.user-actions-container {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 32px;
}

.user-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: stretch;
}

.logout {
  font-size: 0.9rem;
  height: 100%;
  display: flex;
  align-items: center;
  cursor: pointer;
  background: none;
  border: none;
  color: inherit;
  font-family: inherit;
  padding: 0 8px;
}
