@import "../../assets/_design-tokens";

.dtet-button {
  cursor: pointer;
}

.dtet-button:disabled {
  cursor: not-allowed;
  opacity: 0.7;
}

.primary-button {
  background-color: $token-dtet-next-primary-primary;
  color: $token-dtet-next-base-base-white;
  border: none;
  border-radius: 0 var(--radius-4md, 4px) var(--radius-4md, 4px) 0;
  padding: 0 20px;
  font-weight: bold;
}

.primary-button:hover {
  background-color: $token-dtet-next-primary-primary-darker;
}

.primary-button:disabled {
  background-color: $token-dtet-next-disabled-disabled;
}

.outline-button {
  background-color: white;
  color: $token-dtet-next-primary-primary;
  border: 2px solid $token-dtet-next-primary-primary;
  border-radius: 4px;
  padding: 8px 16px;
  font-weight: bold;
  transition:
    background-color 0.2s,
    color 0.2s;
}

.outline-button:hover {
  background-color: #f0f7ff;
}

.secondary-button {
  background-color: $token-dtet-next-base-base-lighter;
  color: $token-dtet-next-primary-primary-darker;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-weight: bold;
  transition:
    background-color 0.2s,
    color 0.2s;
}

.secondary-button:hover {
  background-color: darken($token-dtet-next-base-base-lighter, 5%);
}
