@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Public+Sans:wght@400;500;700&display=swap");
@import "../assets/_design-tokens";

.clinical-service-container {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.services-page {
  font-family: "Source Sans Pro", Arial, sans-serif;
  color: $token-dtet-next-accent-cool-accent-cool-darker;
}

/* Patient Info Styles */
.patient-info-container {
  display: flex;
  flex-direction: column;
  top: 8px;
  width: 100%;
}

.patient-info-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  background-color: $token-dtet-next-base-base-white;
  padding: 20px;
  border-radius: 4px;
  border: 1px solid $token-dtet-next-base-base-lighter;
}

.patient-info-column {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.patient-info-value {
  font-size: 16px;
  font-weight: 400;
  color: #323a45;
}

.service-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.section-title {
  color: $token-dtet-next-base-base-darkest;
  font-size: 14px;
  font-weight: 600;
  margin: 0;
}

.section-content {
  color: $token-dtet-next-accent-cool-accent-cool-darker;
  font-size: 16px;
  margin: 0;
  line-height: 1.4;
}

.info-section {
  background-color: $token-dtet-next-base-base-white;
  padding: 20px;
  border-radius: 4px;
  border: 1px solid $token-dtet-next-base-base-lighter;
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  width: 100%;
}

.info-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.info-label {
  display: flex;
  align-items: center;
  font-weight: 600;
  color: $token-dtet-next-base-base-darkest;
  font-size: 14px;
}

.info-value {
  font-size: 16px;
  font-style: italic;
  font-weight: 400;
  color: #323a45;
}

/* Updated Patient Info Styles */
.patient-info-container {
  width: 100%;
}

.patient-info-section {
  display: flex;
  flex-direction: row;
  background-color: $token-dtet-next-base-base-white;
  border-radius: 4px;
  border: 1px solid $token-dtet-next-base-base-lighter;
  padding: 16px;
}

.patient-info-column {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 12px;
  padding: 0 15px;
  flex: 1;
}

.patient-info-column:first-child {
  padding-left: 0;
}

.patient-info-column:not(:last-child) {
  border-right: 1px solid $token-dtet-next-base-base-lighter;
}

.info-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  color: $token-dtet-next-base-base-white;
  font-size: 12px;
  margin-left: 5px;
}

.temporary-address {
  margin-top: 4px;
  font-size: 14px;
  color: $token-dtet-next-base-base-dark;
}

.address-info-value {
  background-color: $token-dtet-next-info-info-lighter;
  border: 1px solid $token-dtet-next-info-info-darker;
  padding: 6px 8px;
  border-radius: 3px;
}

.address-column {
  justify-content: space-between;
}

.phone-column {
  flex: 0.8; /* Give phone column a bit less space */
}

/* Eligibility Styles */
.eligibility-indicator {
  display: flex;
  align-items: center;
  gap: 10px;
}

.help-icon {
  width: 20px;
  height: 20px;
  fill: #005ea2;
}

.eligibility-status {
  font-weight: 600;
  font-size: 14px;
  color: #323a45;
}

.eligible {
  color: #00a91c;
  background-color: rgba(0, 169, 28, 0.1);
}

.not-eligible {
  color: #b50909;
  background-color: rgba(181, 9, 9, 0.1);
}

/* Form Styles */
.service-selection-section {
  background-color: $token-dtet-next-base-base-white;
  padding: 20px;
  border-radius: 4px;
  border: 1px solid $token-dtet-next-base-base-lighter;
}

.form-dropdown-container {
  display: flex;
  flex-direction: column;
  width: 875px;
}

.form-dropdown {
  display: flex;
  gap: 20px;
  flex-direction: row;
}

.form-group {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.small {
  flex-direction: column;
}

.form-label {
  display: flex;
  margin-bottom: 8px;
  font-weight: 600;
  color: $token-dtet-next-base-base-darkest;
}

.select-wrapper {
  position: relative;
}

.form-select {
  width: 100%;
  padding: 10px;
  border: 1px solid $token-dtet-next-base-base-lighter;
  border-radius: 4px;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16px;
  appearance: none;
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2224%22%20height%3D%2224%22%20viewBox%3D%220%200%2024%2024%22%3E%3Cpath%20d%3D%22M7%2010l5%205%205-5z%22%2F%3E%3C%2Fsvg%3E");
  background-repeat: no-repeat;
  background-position: right 8px center;
}

.time-range-forms {
  display: flex;
  flex-direction: column;
}

.drive-time-label {
  font-weight: 400;
  font-size: 16px;
}

.change-range {
  color: $token-dtet-next-base-base-darkest;
}
.display-in-min-label {
  color: $token-dtet-next-base-base;
}

.range-group {
  display: flex;
  flex-direction: column;
}

.range-input-container {
  display: flex;
  align-items: center;
  gap: 10px;
}

.range-label {
  font-size: 14px;
  color: $token-dtet-next-base-base-darkest;
}

.range-input {
  width: 70px;
  padding: 8px;
  border: 1px solid $token-dtet-next-base-base-lighter;
  border-radius: 4px;
  text-align: center;
}

.form-input {
  height: 40px;
  width: 103px;
}

.refresh-table-button {
  height: 39px;
  width: 206px;
}

.refresh-button {
  height: 39px;
  margin-left: 10px;
}

.required-asterisk {
  color: #e52207;
  margin-left: 3px;
}

.unit {
  font-size: 16px;
  font-weight: 400;
  color: #000000;
  padding-left: 2px;
}

.facilities-container {
  background-color: #ffffff;
}

/* Facilities Table Styles */
.facilities-section {
  background-color: $token-dtet-next-base-base-white;
  padding: 20px;
  border-radius: 4px;
  border: 1px solid $token-dtet-next-base-base-lighter;
}

.facilities-title {
  color: $token-dtet-next-primary-primary-darker;
  font-size: 18px;
  margin-bottom: 16px;
}

.table-title {
  margin-left: 16px;
}

.facilities-table-container {
  overflow-x: auto;
}

.facilities-table {
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
}

.facilities-table th,
.facilities-table td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid $token-dtet-next-base-base-lighter;
  color: $token-dtet-next-base-base-darkest;
}

.facilities-table th {
  color: $token-dtet-next-primary-primary-darker;
  font-family: "Source Sans Pro";
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

/* Select button styles */
.select-button-cell {
  width: 150px;
  text-align: center;
}

.select-header {
  width: 150px;
}

.select-button {
  width: 100%;
  min-width: 120px;
  padding: 8px 16px;
  font-weight: 600;
  text-align: center;
  border-radius: 4px;
}

.no-facilities {
  text-align: center;
  padding: 30px;
  color: $token-dtet-next-base-base-darkest;
  font-style: italic;
}

/* Geolocation Link Styles */
.geolocation-link {
  color: $token-dtet-alert-link-color;
  text-decoration: underline;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
}

.external-link-icon {
  display: flex;
  font-size: 14px;
}
