@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Public+Sans:wght@400;500;700&display=swap");
@import "../assets/_design-tokens";

/* Accessibility-specific styles */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.error-message {
  color: #b50909;
  margin-top: 8px;
  font-weight: bold;
}

.input-error {
  border: 2px solid #b50909;
}

.no-results {
  padding: 20px;
  font-size: 18px;
  font-weight: 600;
  color: $token-dtet-next-primary-primary-darker;
  background-color: #f9f9f9;
  border-radius: 4px;
  text-align: center;
}

/* Reset button styles to be keyboard accessible */
button,
.logout {
  cursor: pointer;
}

/* Original styles from here */
.patient-search {
  font-family: "Source Sans Pro", Arial, sans-serif;
  color: $token-dtet-next-accent-cool-accent-cool-darker;
  background: linear-gradient(270deg, #e2d1c3 0%, #fdfcfb 100%);
}

.header {
  display: flex;
  align-items: center;
  background-color: $token-dtet-next-primary-primary-darker;
  color: $token-dtet-next-base-base-white;
  height: 40px;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: rgba(255, 255, 255, 0.2);
  padding: 0px 8px 0px 8px;
  justify-content: space-between;
  background: $token-dtet-next-accent-cool-accent-cool-darker;
}

.left-header-group {
  display: flex;
  align-items: center;
  flex: 1 0 0;
}

.header-logo {
  font-weight: bold;
  height: 100%;
  display: flex;
  align-items: center;
  margin-right: 32px; /* Add specific margin to create gap with header-title */
}

.community-text {
  color: $token-dtet-next-secondary-secondary-light;
}

.header-title {
  font-weight: bold;
  height: 100%;
  display: flex;
  align-items: center;
}

.patient-search-link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-bottom: 3px solid #c7a97b;
  padding: 0 2px;
  height: 100%;
}

.patient-search-link-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 0 0;
  height: 100%;
  gap: 32px;
}

.patient-search-label {
  color: var(--theme-color-ink, #1b1b1b);
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 8px;
  display: block;
}

.required-asterisk {
  color: #e52207;
}

.user-actions-container {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 32px;
}

.user-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: stretch;
}

.logout {
  // padding: 0 15px;
  font-size: 0.9rem;
  height: 100%;
  display: flex;
  align-items: center;
  cursor: pointer;
  background: none;
  border: none;
  color: inherit;
  font-family: inherit;
  padding: 0 8px;
}

.main-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 40px;
  // height should go to bottom of page
  min-height: calc(100vh - 40px);
  padding: 40px;
}

.dtet-header {
  display: flex;
  width: 1360px;
  flex-direction: column;
  align-items: flex-start;
  gap: 1px;
}

.uswds-prose {
  color: $token-dtet-next-base-base-darkest;
  font-family: var(--usa-font-family-heading, "Public Sans");
  font-size: var(--component-prose-h-1-font-size, 40px); /* TODO: Design tokens */
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  align-self: stretch;
  margin: 0;
}

.usa-prose-intro {
  align-self: stretch;
  color: var(--theme-color-ink, #1b1b1b);
  font-family: var(--usa-font-family-alt, "Public Sans");
  font-size: var(--theme-font-size-alt-xl, 32px);
  font-style: normal;
  font-weight: 400;
  line-height: 162%;
  display: flex;
  padding-top: 20px;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
}

.search-container {
  display: flex;
  width: 516px;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--space-4px, 4px); /* TODO: Design tokens */
}

.search-container label {
  display: block;
  margin-bottom: 10px;
}

.search-input-container {
  display: flex;
  width: 100%;
}

.search-form {
  width: 100%;
}

.search-input {
  flex-grow: 1;
  padding: 8px;
  border: 1px solid $token-dtet-next-base-base-lighter;
  border-radius: var(--radius-4md, 4px) 0 0 var(--radius-4md, 4px); /* TODO: Design tokens */
  font-size: var(--sm---5);
}

.search-results {
  overflow-x: auto;
  display: flex;
  padding: 24px 16px 16px 16px;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  align-self: stretch;
  border-radius: 4px;
  border: 1px solid $token-dtet-next-base-base-lighter;
  background: $token-dtet-next-base-base-white;
}

.patients-table {
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;

  th,
  td {
    padding: 12px;
    text-align: left;
    border-bottom: 1px solid $token-dtet-next-base-base-lighter;
    color: #212121;
  }

  th {
    font-weight: bold;
  }
}

.table-cols {
  color: $token-dtet-next-primary-primary-darker;
  font-family: "Source Sans Pro";
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  display: table-header-group;
  border-bottom: 1px solid $token-dtet-next-base-base-lighter;
}

.table-container {
  width: 100%;
  overflow-x: auto;
}

.table-title {
  color: $token-dtet-next-primary-primary-darker;
  font-family: "Source Sans Pro";
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.load-more-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  padding-bottom: 20px;
}

.all-results-message {
  color: $token-dtet-next-primary-primary-darker;
  font-family: "Source Sans Pro";
  font-size: 24px;
  padding: 10px;
  font-weight: 400;
}

.patient-name-link {
  color: $token-dtet-alert-link-color;
  text-decoration: underline;
  cursor: pointer;
}

.sortable-header {
  cursor: pointer;
  position: relative;
  padding-right: 24px; /* Increased padding for SVG icons */
  user-select: none;

  /* Remove the existing ::after pseudo-element that adds text arrows */
  &::after {
    content: none;
  }

  /* Apply background color to the sorted column header */
  &[aria-sort="ascending"],
  &[aria-sort="descending"] {
    background-color: $token-dtet-table-sorted-header-background-color;
  }
}

/* Style for cells in the sorted column */
.patients-table td.sorted-column {
  background-color: $token-dtet-table-sorted-stripe-background-color;
}

.sort-indicator {
  margin-left: 8px;
  vertical-align: middle;
  width: 16px;
  height: 16px;
  display: inline-block;
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);

  &.asc {
    /* Flip the arrow_downward.svg on Y axis for ascending sort */
    transform: translateY(-50%) rotate(180deg);
  }
}
